body {
  background-color: #1F1F1F;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-header {
  background-color: #1f1f1f;
  box-shadow: inset 0 0 7px black;
  display: inline-flex;
  height: 4rem;
  justify-content: space-between;
  left: 0;
  position: fixed;
  width: 100%;
  z-index: 3;
}

.main-header button {
  border: none;
  background-color: transparent;
  color: #fcfcfc;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  margin: 0 1rem;
  outline: none;
}

/*.main-header button:hover {
  color: #c2c2c2;
}*/

.nav-link {
  color: #fcfcfc;
  text-decoration: none;
}

.nav-link:hover {
  color: #c2c2c2;  
}

.all-favorites {
}

.header-title {
  display: inline-block;
  height: 3rem;
  margin: 8px;
}

.main-logo {
  cursor: pointer;
  height: 3rem;
  margin-right: 5px;
}

.header-text {
  color: #fcfcfc;
  cursor: pointer;
  display: inline-block;
  font-family: 'Oleo Script', cursive;
  font-size: 2rem;
  font-weight: 400;
  margin: 0;
  vertical-align: 14px;
}

.sign-out {
}
.CardContainer {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-column-gap: 1rem;
	-webkit-column-gap: 1rem;
	        column-gap: 1rem;
	margin: 0 auto;
	width: calc(100% - 2px);
}

@media screen and (max-width: 1000px) {
	.CardContainer {
		grid-template-columns: repeat(2, 1fr);
		margin: 0 auto;
	}
}

@media screen and (max-width: 730px) {
	.CardContainer {
		display: block;
		margin: 0 auto;
	}
}

.header-container-splitter {
	height: 4.5rem;
}


.Card {
	align-items: center;
	background-color: #fcfcfc;
	background-position: top center;
	background-size: cover;
	box-shadow: 0 8px 1rem rgba(0, 0, 0, .5);
	cursor: pointer;
	display: flex;
	flex-direction: column;
	height: 20rem;
	justify-content: space-between;
	margin: 1rem auto;
	padding: 0;
	width: 100%;
	-webkit-transition-duration: .1s;
	        transition-duration: .1s;
}

.Card:hover {
	box-shadow: 0 12px 1rem rgba(0, 0, 0, .5);
}

.card-inner-wrapper {
	width: 100%;
}

.Card p {
	display: none;
}

.Card:hover .movie-title {
	background: -webkit-linear-gradient(top, rgba(0, 0, 0, .9), rgba(0, 0, 0, .6), transparent);
	background: linear-gradient(to bottom, rgba(0, 0, 0, .9), rgba(0, 0, 0, .6), transparent);
}

.movie-rating {
	font-size: 1rem;
	height: 2.5rem;
	margin-left: 1rem;
	width: 2.5rem;
}

.card-favorite-button {
	background-color: #222;
	border: none;
  bottom: 8px;
  box-shadow: inset 0 0 4px rgba(0, 0, 0, .4);
  -webkit-clip-path: polygon(0 0, 100% 100%, 100% 0);
          clip-path: polygon(0 0, 100% 100%, 100% 0);
	cursor: pointer;
  height: 70px;
	outline: none;
	position: relative;
	right: 1px;
	-webkit-transition-duration: .2s;
	        transition-duration: .2s;
  width: 70px;
}

.card-favorite-button:hover {
	background-color: #444;
}

.card-favorite-button img {
	bottom: 15px;
	height: 2rem;
	left: 15px;
	position: relative;
}

.fav-btn-active {
	background-color: #444;
}

.fav-btn-active:hover {
	background-color: #222;
}

.movie-title {
	background: -webkit-linear-gradient(top, rgba(0, 0, 0, .8), rgba(0, 0, 0, .5), transparent);
	background: linear-gradient(to bottom, rgba(0, 0, 0, .8), rgba(0, 0, 0, .5), transparent);
	color: #fcfcfc;
	display: inline-flex;
	font-size: 2rem;
	justify-content: space-between;
	height: 4rem;
	padding: .5rem 0;
	text-shadow: 0 0 2rem rgba(0, 0, 0, .9);
	margin: 0;
	overflow: hidden;
	width: calc(100% + 1px);
}

.Card p {
	margin: 0;
}

.poster-image {
	display: none;
	width: 15rem;
}

/* --------------- EXPANDED CARD --------------- */

.expanded-background {
	display: none;
}

.expanded-card {
	height: 80%;
	margin-left: 10vw;
	position: fixed;
	width: 80%;
	z-index: 3;
}

.expanded-card .card-inner-wrapper {
}

.expanded-card .expanded-background {
	background-color: rgba(0, 0, 0, .4);
	display: inline;
	display: initial;
	height: 100vh;
	left: 0;
	position: fixed;
	top: 0;
	width: 100vw;
	z-index: -1;
}

.expanded-card .movie-title {
	padding-top: 2rem;
}

.expanded-card .card-favorite-button {
	background-color: transparent;
	width: 10%;
}

.expanded-card .card-inner-wrapper {
	background-color: transparent;
	height: 100%;
}

.expanded-lower-container {
	display: inline-flex;
	justify-content: space-around;
	height: 80%;
}

.expanded-card .poster-image {
	box-shadow: 0 0 3rem #666;
	border-radius: 5px;
	display: inline;
	height: 24rem;
	margin-top: 3rem;
	width: 16rem;
}

.expanded-card p {
	color: white;
	display: inline;
	padding-top: 4rem;
}

.rating-container {
  background-color: #fcfcfc;
  border-radius: 2rem;
  height: 2rem;
  margin: 3rem auto;
  padding: 2px;
  width: 100%;
}

.rating-bar {
	border-radius: 1rem;
	font-size: 1rem;
	font-weight: 700;
	line-height: 200%;
	text-align: center;
  height: 32px;
}

.lower-middle-expanded {
	text-align: justify;
	margin-top: 2rem;
	width: 45%;
}

.card-overview {
	font-size: 2rem;
}

.card-release-date {
	font-size: 1.5rem;
	text-align: end;
}

/* --------------- MEDIA QUERIES --------------- */

@media screen and (max-width: 1600px) {
  .Card {
		height: 17rem;
	}

	.movie-title {
		position: relative;
		bottom: 9px;
	}

	.card-favorite-button {
		right: 0;
	}

	.movie-title {
		font-size: 1.5rem;
		width: 100%;
	}

	.expanded-card {
		height: 70%;
		margin-top: 2rem;
	}

	.expanded-card p {
		font-size: 1.5rem;
	}

	.expanded-card .poster-image {
		margin: 0;
	}
}

@media screen and (max-width: 900px) {
	.Card {
		height: 13rem;
	}

	.card-favorite-button {
		left: 1px;
	}

	.expanded-card p {
		font-size: 15px;
	}
}

@media screen and (max-width: 730px) {
	.Card {
		border-radius: 0;
		height: 15rem;
		margin: 0;
		width: 100%;
	}

	.movie-title {
		border-radius: 0;
	}
}
.Login {
  background-image: url(/movie-tracker/static/media/movies.05a753d9.jpg);
  padding-top: 30vh;
  position: relative;
  width: 100%;
  height: 100vh;
}

.Login::after {
  content: '';
  background-color: #000;
  opacity: 0.86;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.Login-form {
  background-color: #f2f2f2;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, .4);
  border-radius: 10px;
  color: #222;
  height: 25rem;
  padding: 5px;
  position: relative;
  margin: auto;
  width: 20%;
  min-width: 32rem;
  z-index: 1;
}

.Login-header {
  font-family: 'Oleo Script', cursive;
}

.Login input,
.Login button {
  border-radius: 2px;
  border: none;
  box-shadow: none;
  display: block;
  font-size: 18px;
  margin: 1rem auto;
  padding: 8px 1rem;
  outline: none;
  -webkit-transition-duration: .2s;
          transition-duration: .2s;
  width: 24rem; /* same as 416px, 24rem, or 75% */
}

.Login input {
  background-color: #fcfcfc;
}

.Login input:focus {
  background-color: #FFFFDC;
}

.Login button {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .3);
  cursor: pointer;
  font-weight: 600;
  width: 416px;
}

.create-user {
  background-color: #d2d2d2;
  color: #222;
}

.Login button:hover {
  box-shadow: 0 1px 2px rgba(0, 0, 0, .6);
}

.login-button {
  background-color: #ff3030;
  color: #fcfcfc;
  font-weight: 600;
}

.home-link {
  color: #222;
  text-decoration: none;
}
.App {
  text-align: center;
  background-color: #222;
}


h1 {
  margin: 0;
  padding: 0;
}
html, body, .app {
  height: 100vh;
  margin: 0;
  padding: 0;
  position: relative;
  width: 100%;
}
.ErrorMessage {
  background-image: url(/movie-tracker/static/media/movies.05a753d9.jpg);
  height: 100vh;
  padding-top: 30vh;
  position: relative;
  width: 100%;
}

.ErrorMessage::after {
  content: '';
  background-color: #000;
  left: 0;
  height: 100%;
  opacity: 0.86;
  position: absolute;
  top: 0;
  width: 100%;
}

.ErrorMessage_inner {
  align-items: center;
  background-color: #f2f2f2;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, .4);
  border-radius: 10px;
  color: #222;
  display: flex;
  flex-direction: column;
  height: 25rem;
  justify-content: center;
  padding: 5px;
  position: relative;
  margin: auto;
  width: 20%;
  min-width: 32rem;
  z-index: 1;
}

.ErrorMessage button {
  border-radius: 2px;
  border: none;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .3);
  cursor: pointer;
  display: block;
  font-size: 18px;
  font-weight: 600;
  margin: 1rem auto;
  padding: 8px 1rem;
  outline: none;
  -webkit-transition-duration: .2s;
          transition-duration: .2s;
  width: 24rem;
}

.home-button-error {
  background-color: #ff3030;
  color: #fcfcfc;
}

.login-button-error {
  background-color: #d2d2d2;
  color: #222;
}

.home-link-error {
  color: #fcfcfc;
  text-decoration: none;
}

.login-link-error {
  color: #222;
  text-decoration: none;
}

